/* General settings */
body {
    padding: 0;
    font-size: 100%;
    line-height: 1.5;
    color: #555;
    font-family: $body-font;
}

article,
aside,
figcaption,
figure,
footer,
header,
nav,
section {
    display: block;
}

h1,
h2,
h3,
h4,
stong {
    margin-top: 1em;
    padding-top: 1em;
    line-height: 1.25;
    color: $body-colour;
    font-family: $header-font;
}

h1 {
    font-size: 2.5rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.5rem;
}

h4 {
    font-size: 1rem;
}

ol,
ul {
    padding-left: 40px;
}

figure,
ol,
p,
ul {
    margin: 1em 0;
}

a img {
    border: 0;
}

sub,
sup {
    line-height: 0;
}
